/* common */
*{outline: none;text-decoration: none;}
a:hover{text-decoration: none !important;}
p,a{color: #485463;}

.checkbox .input-helper:before,.radio .input-helper:before{left:0;}
.checkbox .input-helper:before,.radio .input-helper:before,.checkbox .input-helper:after,.radio .input-helper:after{-webkit-transition:all;-o-transition:all;transition:all;-webkit-transition-duration:250ms;-o-transition-duration:250ms;transition-duration:250ms;-webkit-backface-visibility:hidden;backface-visibility:hidden;position:absolute;content:""}
.checkbox,.radio{display:inline-block;position:relative;vertical-align:middle;padding:0 0 0 25px;min-height:20px}
.ion-connection-bars:before{content:"\f274";display:block;margin-top:3px}

.checkbox input,.radio input{top:0;left:0;margin:0!important;z-index:1;cursor:pointer;opacity:0;filter:alpha(opacity=0)}
.radio input[type="radio"],.radio-inline input[type="radio"],.checkbox input[type="checkbox"],.checkbox-inline input[type="checkbox"]{position:absolute;margin-left:-20px;margin-top:4px \9}
.checkbox.disabled,.radio.disabled{opacity:.6;filter:alpha(opacity=60)}
.checkbox input{width:17px;height:17px}
.checkbox input:checked + .input-helper:after{-webkit-transform:scale(1) rotate(-50deg);-ms-transform:scale(1) rotate(-50deg);transform:scale(1) rotate(-50deg);opacity:1;filter:alpha(opacity=100)}
.checkbox .input-helper:before{top:4px;width:18px;height:18px;border-radius:3px;background:none;    border: 1px solid #888;}
.checkbox input:checked + .input-helper:before{background-color:#f16622;border-color: #f16622 }
.checkbox .input-helper:after{opacity:0;filter:alpha(opacity=0);-webkit-transform:scale(0) rotate(80deg);-ms-transform:scale(0) rotate(80deg);transform:scale(0) rotate(80deg);width: 10px;height: 5px;border-bottom:2px solid #fff;border-left:2px solid #fff;left: 4px;top:9px;}

/* top bar */
.logo-top-outer{margin-top: 70px;}
.logo-container{display: flex;justify-content: space-between;align-items: center;padding:20px 15px; width: calc(100% - 260px); background: #fff; position: fixed;top: 0;left: 0;z-index: 99;margin-left: 260px;box-shadow: 0 0px 5px rgba(0,0,0,.15);}
.logo-container .logo img{max-width: 120px;}
.logo-container .logo {width:140px ;}
.logo-container  .searchBox{width: 45%;display: flex;}
.logo-container .add-post{width: 20%; text-align: center;}
.logo-container  .searchBox .search-outer{width: calc(100% - 130px);border-radius: 2px;
    position: relative;padding: 0 10px 0px 40px;    -webkit-transition: .3s;-o-transition: .3s;transition: .3s;box-shadow: 0 5px 10px 0 rgba(0,0,0,.15);background: #fff;}
 .logo-container  .searchBox .search-outer input.search-box {background: none;border: none;color: #000;border: none;padding-left: 10px;outline: none;width: 100%;
   height: 40px;font-size: 15px;}
 .logo-container .searchBox .search-outer .search {background: url('../images/search.png') no-repeat;width: 21px;height: 18px;background-size: contain;position: absolute;left: 10px;top: 50%;cursor: pointer;transform: translate(0, -50%);border: none;
}
.logo-container .locationDropDown{margin-left: 10px;width: 130px;}
.logo-container .locationDropDown select{padding: 5px;padding-left: 20px;border: none;background: url('../images/location.png') no-repeat calc(5px) center / 16px; background-attachment: fixed;
}
.logo-container .post-add{background: -webkit-linear-gradient(left, rgba(242, 101, 34, 0.9) 0%,rgba(242, 101, 34, 0.9) 24%,rgba(237, 20, 91, 0.96) 77%,rgba(237, 20, 91, 0.96) 100%); color: #fff; border: none;padding: 5px 15px;}
.logo-container .login-section{display: flex;align-items: center;width: 20%; justify-content: flex-end;}
.logo-container .login-section .userIcon{margin-right: 10px;}
.logo-container .login-section .userImage{max-width: 25px;margin-right: 10px;border-radius: 50%;}
.logo-container .login-section a{color: #2d3748;}

/* mainContainer */

.main-container{margin-left: 260px;padding-left: 15px;margin-top: 120px;margin-right: 15px;}

/* Nav Bar */

.navBar{padding:15px;text-align: center;background: #fff}
.navBar ul{list-style: none;padding:0;margin:0}
.navBar ul li{display: inline-block;vertical-align: middle;}
.navBar ul li a{padding: 5px 15px;color: #2d3748; text-transform: uppercase;font-size: 14px;transition: 0.3s all;}
.navBar ul li a:hover{color:#ed145b; text-decoration: none;transition: 0.3s all;}
.navBar .arrowIcon{margin-left:4px;}

/* Banner Section */

.banner-section{background: url('../images/banner.jpg') no-repeat; background-size: cover;padding:100px 25px; position: relative;padding-bottom: 150px;}
.banner-section:before{content:''; position: absolute; top: 0; left: 0;height: 100%;width: 100%;background: rgba(0,0,0,.8)}
.banner-section .text-Box{z-index: 1;position: relative;color: #fff;text-align: center;    max-width: 700px;margin: 0 auto;}
.banner-section h2{font-size: 40px;text-transform: capitalize;letter-spacing: 1px;margin-bottom:15px ;}
.banner-section p{color: #fff;text-align: center;z-index: 1;display: inline-block; font-size: 22px;font-weight: 500;line-height:26px;margin-bottom: 0;}
.banner-section .subText{font-weight: normal;}
.banner-section .Typewriter{display: inline-block;}

.how-it-works{max-width: 80%;margin: 0 auto;margin-top: -45px;}
ul.grid-4 {display: flex;align-content: flex-start;flex-wrap: wrap;}
ul.grid-4 li{width:25%;padding:0 10px 10px;}

ul.grid-3 {display: flex;align-content: flex-start;flex-wrap: wrap;}
ul.grid-3 li{width:33.33%;padding:0 10px 10px;}
ul.grid-3 .card,ul.grid-3 .card{padding:15px; box-shadow: 0 5px 10px 0 rgba(0,0,0,.15);border: none;text-align: center;}
.how-it-works h4{margin-bottom: 10px;color: #ed145b ;}
.how-it-works p{margin: 0;opacity: 0.8;}
.how-it-works img{max-width: 35px;position: absolute;top: 50%;left:50%;transform:translate(-50%,-50%)}
.how-it-works .image-outer{display: inline-block;background-color: white;height: 60px;width: 60px;margin: 0 auto;border: 1px solid #ed145b;border-radius: 50%;position: relative;margin-bottom: 15px;}


/* prodcuts */

.products-outer{max-width: 95%;margin: 0 auto;background: #fff; border-radius: 5px;padding: 10px; margin-top:15px;}
.products-outer h2{font-size: 18px;margin-bottom: 15px;padding: 10px}
.products-outer ul.grid-3 .card{text-align: left;padding: 0}
.products-outer .card h5{color:#ed145b; font-size: 18px;margin-bottom: 5px;}
.products-outer .card .productInfo{padding: 15px}
.products-outer .card p{margin-bottom: 0;}
.products-outer .card .productImage{width: 100%;max-height: 200px;}
.products-outer .card .price{color:#ed145b; font-weight: 500;font-size: 16px;margin-bottom: 5px;}
.products-outer .card .category{font-size: 14px;color: #ababab;}
.products-outer .card .timeDate{font-size: 12px;color: #706f6f;}
.products-outer .card .category:hover{color: #ed145b;}
.products-outer .card .description{font-size: 14px;color: #ababab; white-space:nowrap; overflow:hidden;text-overflow: ellipsis;}
.products-outer .card .product-btn{width: 100%; background: #ed145b;border: none;padding: 5px}
.products-outer .card .btns{margin-top: 15px}
.products-outer .card .flexRow{display: flex; justify-content: space-between ;align-content:center;}

/* sidebar */

.side-bar-outer{}
.sidebar-header{background: #fff;padding: 30px 15px;}
.toggle-btn{width: 32px;padding: 5px;cursor: pointer;display:none}
.toggle-btn span {display: block; height:3px; background:#ed145b; margin:5px 0;-webkit-transition: 0.2s;-o-transition: 0.2s;transition: 0.2s;}
.osso-outer.open .toggle-btn .stick2{opacity:0;} 
.osso-outer.open .toggle-btn .stick1{-webkit-transform:rotate(45deg);-ms-transform:rotate(45deg);transform:rotate(45deg); margin-top:13px;}
.osso-outer.open .toggle-btn .stick3{-webkit-transform:rotate(-45deg);-ms-transform:rotate(-45deg);transform:rotate(-45deg); margin-top:-16px;}

.sidebar-header .logo-img{text-align: center;}
.sidebar-header .logo-img img{max-width: 80%;}
.sidebar{z-index: 100;background: #2d2e30;width: 260px;position: fixed;top:0px;height:100%; box-shadow: 0 100px 10px rgba(0,0,0,0.4);}

.sidebar li a{display: block; color: #fff; padding: 10px 15px;transition: 0.3s all;border-bottom: 1px solid #515151;}
.sidebar li a:hover{background: #ed145b;text-decoration: none;transition: 0.3s all;}
.sidebar li a:after{content:''; display:block; clear:both}

.sidebar li a span{float: right;}
.sidebar li .arrowBtn{transition: 0.3s all};
.sidebar li:hover a span{}
.sidebar li a:hover .arrowBtn{color: #fff !important;transform: rotate(90deg);transition: 0.3s all;}


/* drop menu */

.drop-menu{margin-left: 10px;transform: scaleY(0);transform-origin: top; position: absolute;}
.sidebar li:hover .drop-menu{transform: scaleY(1);position: relative;transition: 0.3s all;}
.drop-menu li a{border-left: 2px solid #515151; font-size: 14px;}

.sub-drop-menu{margin-left: 10px;transform: scaleY(0);transform-origin: top; position: absolute;}
.sidebar .drop-menu li:hover .sub-drop-menu{transform: scaleY(1);position: relative;transition: 0.3s all;}
.sub-drop-menu li a{border-left: 2px solid #515151; font-size: 14px;}

/* Login SignUP */

.login-outer{max-width: 800px;margin: 0 auto;margin-top:35px;background: #fff;border-radius: 5px;padding:40px 25px;box-shadow: 0 5px 10px 0 rgba(0,0,0,.15);}
.form-heading{font-size: 22px;letter-spacing: 1px;margin-bottom: 30px;}
.form-element{margin-bottom: 30px;}
.submit-wrap{margin: 35px 0;}
.submit-wrap a{display: block;text-align: center;background-color: #f16622;border-radius: 4px;box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.07);color: #fff;padding: 18px 15px;font-size: 16px;font-weight: 700;transition:0.3s all;}
.submit-wrap a:hover{transition:0.3s all;background: #000;color: #fff;}
input{background-color: #F6F6F7;border-radius: 2px;box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.07);width: 100%;padding:18px 15px;font-weight: 500;font-size: 16px;line-height: 22px;border: 1px solid transparent;transition:0.3s all;filter: none;color:#454545}
input:focus{border: 1px solid #f16622;transition:0.3s all;}

.row-section a{display: inline-block;text-align: right;font-weight: 700;line-height: 20px;font-size: 14px;color: #000000;opacity: 0.5;float:right;transition:0.3s all;}
.row-section .remember-me{float: left;}
.row-section a:hover{color: #000;transition:0.3s all;}
.row-section:after{content: '';display: block;clear: both;}
.redirect-link{text-align: center;}
.redirect-link a{color: #f16622}

/* profile */

/*upload image*/
.upload-img{padding:0px;width:150px; height: 150px; border:1px solid #e0e0e0; overflow: hidden;border-radius: 5px;position: relative;}
.upload-img .img-border {width:100%;height:100%; background:url('../images/default.png') no-repeat center center;text-align: center;position: relative;font-size: 1.1em;font-weight: 500;padding-top: 0px;overflow: hidden;display: inline-block;vertical-align: middle;margin-right: 10px; background-size: cover; }
.upload-img .img-border:hover{opacity:0.8;}
.upload-img .upload-photo {width:calc(100% + 20px) ;height:calc(100% + 20px);text-align: center;position: absolute;cursor: pointer;top:-20px;left: -20px;outline: none;opacity: 0;border-radius: 50%;cursor: pointer; }
.upload-img .img-border img {vertical-align: top;height: 150px;width: 150px;max-width: 100%;}
#priview_image .close-img{position: absolute; top:0; right: 0px; background: #f16622; border-radius:0 0 0 3px; color: #fff; font-size: 16px; padding:5px 8px;}
.upload-img .edit-img{position: absolute; top:0; right: 0px; background: #f16622; border-radius:0 0 0 3px; color: #fff; font-size: 16px; padding:5px 8px;}
.upload-img .img-border:hover .close-img{opacity: 0; transition: 0.3s all;}

